.card {
  width: 70%;
  margin: 50px auto;
}

h3 {
  padding-top: 8px;
}

.navbar {
  padding-left: 0;
  padding-bottom: 0;
  margin-bottom: 0;
}

.card-header .navbar .nav-link {
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 0;
  margin-right: 25px;
}

@media only screen and (max-width: 767px) {
  .card {
    width: 85%;
    margin: 40px auto;
  }
}
