.product-links .container {
  padding-right: 0;
  padding-left: 0;
}

.product-links .col-md-8 {
  padding-right: 0;
}

.product-links .btn-primary {
  width: 100%;
}

.product-links .list-group-item {
  padding-top: 20px;
  padding-bottom: 25px;
}

.product-links .dropdown {
  display: inline-block;
  margin-left: 15px;
  margin-bottom: 25px;
}

@media only screen and (max-width: 767px) {
  .product-links .col-md-8 {
    padding-right: 15px;
    margin-bottom: 15px;
  }
}
